import React from 'react';
import './Scss/navbar.scss';
import Fade from 'react-reveal/Fade';

const Navbar = ({item, addLanguage}) => {

    const btnNavbar = () => {
        let ulNavbar = document.querySelector('.navbar-nav');
        let deleteNav = document.querySelector('.delete-nav');
        let black = document.getElementById('black-body');
        black.classList.add('black');        
        ulNavbar.classList.add('saw');
        deleteNav.classList.remove('d-none')
    }

    const deleteNavbar = () => {
        let ulNavbar = document.querySelector('.navbar-nav');
        let black = document.getElementById('black-body');
        let deleteNav = document.querySelector('.delete-nav');
        black.classList.remove('black');   
        ulNavbar.classList.remove('saw')
        deleteNav.classList.add('d-none')
    }

    const liTools = () => {
        let ulNavbar = document.querySelector('.navbar-nav');
        let black = document.getElementById('black-body');
        black.classList.remove('black');   
        ulNavbar.classList.remove('saw')
    }

    const languageOptions = [
        { key: 'Eng', text: 'Eng', value: 'Eng' },
        { key: 'Ru', text: 'Ru', value: 'Ru' },
        { key: 'Uzb', text: 'Uzb', value: 'Uzb' },
      ]

    window.addEventListener("scroll",function () {
        let navScroll = document.getElementById('nav');
        navScroll.classList.toggle("crimson",window.scrollY>10);
    });

    return (
        <div id="nav" className="Navbar fixed-top">
            <div className="container-fluid">
                    <nav className="navbar navbar-expand-md">
                        <div className="container-fluid">
                        <Fade top><a href="#" className="navbar-brand"><h3>Safar<span>Ali</span></h3></a></Fade>
                            <ul className="navbar-nav">
                                <button className="delete-nav d-none" onClick={deleteNavbar}>X</button>
                                <Fade top><li onClick={liTools} className="nav-item mx-2"><a href="#home" className="nav-link active"><i className="home icon"></i> Home</a></li></Fade>
                                <Fade top><li onClick={liTools} className="nav-item mx-2"><a href="#about" className="nav-link"><i className="address book icon"></i> About</a></li></Fade>
                                <Fade top><li onClick={liTools} className="nav-item mx-2"><a href="#work" className="nav-link"><i className="folder open icon"></i> Projects</a></li></Fade>
                                <Fade top><li onClick={liTools} className="nav-item mx-2"><a href="#skills" className="nav-link"><i className="chart bar icon"></i> Skills</a></li></Fade>
                                <Fade top><li onClick={liTools} className="nav-item mx-2"><a href="#contacts" className="nav-link"><i className="mail icon"></i> Contacts</a></li></Fade>
                            </ul>  
                            {/* <select className='lang'>
                                {item.map((category) => {
                                    return(
                                        <option onClick={() => addLanguage(category)} value={category.key}>{category.key}</option>
                                    )
                                })}
                            </select> */}

                            <div id="button-navbar" className="btn button-navbar" onClick={btnNavbar}><i className="bars icon"></i></div>
                        </div>
                    </nav>
            </div>  
            <div id="black-body" className="black-body"></div>    
        </div>
    );
};

export default Navbar;