import React from 'react';
import  Navbar  from '../Navbar/Navbar';
import './Scss/home.scss';
import Ali from './images/home2.png';
import { Col, Row } from 'reactstrap';
import Fade from 'react-reveal/Fade';

const Home = ({item, addLanguage}) => {

    window.addEventListener("scroll",function () {
        let arrowScroll = document.getElementById('scroll-up');
        if(window.scrollY > 400){
        arrowScroll.classList.add("show")
        }
        else{
            arrowScroll.classList.remove("show")
        }
    });

    return (
        <div id="home" className="Home">
            <Navbar item={item} addLanguage={addLanguage} />
            <Row>
                <Fade bottom>
                    <Col md="6" className="identify-img">
                        <div className='circle-identify'>
                            <img src={Ali} />
                        </div>
                    </Col>
                </Fade>
                <Col md="6" className="home-text">
                    <Fade left><h1>Safarali <span>Jumanov</span></h1></Fade>
                    <Fade left><h3 className="mt-3">Frontend Developer</h3></Fade>
                    <Fade left><p className="mt-3 col-md-10">I'm Safarali and I'm a frontend developer. I'm from Uzbekistan. Welcome to my personal website!</p></Fade>
                    <Fade bottom>
                    <div className='home-contact'>
                        <a href="https://github.com/alijumanov" target="_blank"><i className='github icon'></i></a>
                        <a href="https://instagram.com/jumanov.safarali" target="_blank"><i className='instagram icon'></i></a>
                        <a href="https://t.me/fdhrgdhry_frontend_master" target="_blank"><i className='telegram icon'></i></a>
                    </div>
                    </Fade>
                </Col>
                <Col className="identify">
                    <img src={Ali} />
                </Col>
            </Row>
                <a href="#home" id="scroll-up" className="scroll-up">
                    <i className="arrow circle up icon"></i>
                </a>
        </div>
    );
};

export default Home;