import React from 'react';
import { Col, Row } from 'reactstrap';
import './Scss/contacts.scss';
import emailjs from 'emailjs-com';
import { Fade, Zoom } from 'react-reveal';

const Contacts = () => {

    function sendEmail(e){
        e.preventDefault();
        emailjs.sendForm('service_ddy2mbh', 'template_eg51ik6', e.target, 'user_0dwGRTX5PZ6VFfH0tdOLF').then(res => {
            console.log(res);
        }).catch(err => console.log(err));
        e.target.reset()
    }

    return (
        <div id="contacts" className="Contacts">
            <div className="container-fluid">
                <Col md="12" className="text-center">
                    <Fade bottom>
                        <h1>Contact me</h1>
                    </Fade>
                </Col>
                <Row>
                    <Col md="6" className="contact-tools mt-3">
                        <Zoom down-left>
                        <div className="shadow">
                            <div className="d-flex-body">
                                <Col md="6">
                                <div className="tools col-12">
                                    <i className="user icon"></i> 
                                    &nbsp; &nbsp; &nbsp;
                                    <span>
                                        <h4>Name</h4>
                                        Safarali Jumanov
                                    </span>
                                </div>
                                <div className="tools col-12">
                                    <i className="mail icon"></i>
                                    &nbsp; &nbsp; &nbsp;
                                    <span>
                                        <h4>Email</h4>
                                        jumanovsafarali@gmail.com
                                    </span>
                                </div>
                                <div className="tools col-12">
                                    <i className="phone icon"></i>
                                    &nbsp; &nbsp; &nbsp;
                                    <span>
                                        <h4>Phone number</h4>
                                        +998 94 913 15 01
                                    </span>
                                </div>
                                </Col>
                                <Col md="6">
                                <div className="tools col-12">
                                    <i className="map marker alternate icon"></i>
                                    &nbsp; &nbsp; &nbsp;
                                    <span>
                                        <h4>Address</h4>
                                        Syrdarya, Uzbekistan
                                    </span>
                                </div>
                                <a href="https://github.com/alijumanov/alijumanov" target="_blank" className="tools col-12">
                                    <i className="github icon"></i>
                                    &nbsp; &nbsp; &nbsp;
                                    <span>
                                        <h4>Github</h4>
                                        alijumanov
                                    </span>
                                </a>
                                <div className="tools col-12">
                                    <i className="telegram icon"></i>
                                    &nbsp; &nbsp; &nbsp;
                                    <span>
                                        <h4>Telegram</h4>
                                        @ali_web_dev
                                    </span>
                                </div>
                                </Col>
                            </div>
                            <Col md="12" className="">
                            <a href="https://instagram.com/jumanov.safarali" target="_blank" className="tools col-12 d-flex justify-content-center mb-4">
                                <i className="instagram icon"></i>
                                &nbsp; &nbsp; &nbsp;
                                <span>
                                    <h4>Instagram</h4>
                                    jumanov.safarali
                                </span>
                            </a>
                            </Col>
                        </div>
                        </Zoom>
                    </Col>
                    <Col md="6" className="mt-3">
                        <Zoom down-right>
                        <form onSubmit={sendEmail} className="contact-form shadow">
                            <Row className="p-0">
                                <Col md="6">
                                    <label className="form-label">Name</label>
                                    <input className="form-control name" type="text" name="name" />
                                </Col>
                                <Col md="6">
                                    <label className="form-label">Email</label>
                                    <input className="form-control email" type="email" name="user_email" />
                                </Col>
                            </Row>
                            <Col md="12">
                                <label className="mt-3 form-label">Message</label>
                                <textarea className="form-control text" name="message" rows="4" />
                            </Col>
                            <Col md="12">
                                <input className="mt-3 btn btn-block w-100" type="submit" value="Send Message" />
                            </Col>
                        </form>
                        </Zoom>
                    </Col>
                </Row>
            </div>
        </div>
    );
};

export default Contacts;