import React, { useState, useEffect } from 'react';
import './Scss/about.scss';
import { Col, Row } from 'reactstrap';
import ABoutImg from './images/about2.png';
import Resume from './images/resume.jpg';
import { Fade, Zoom } from 'react-reveal';
import db from '../Firebase/Config';

const About = () => {

    const [portfolios, setPortfolios] = useState([]);

    useEffect(() => {
        db.collection("portfolio").onSnapshot((snapshot) =>  
            setPortfolios(snapshot.docs.map((doc) => doc.data()))
        );
    }, [])

    return (
        <div id="about" className="About">
            <div className="container-fluid">
                    <Col md="12" className="text-center">
                        <Zoom down>
                            <h1>About Me</h1>
                        </Zoom>
                    </Col>
                <Row>
                    <Col md="6" className="img" data-aos="fade-up-right">
                        <Fade left>
                            <div className="about-img">
                                <div className="back-img">
                                    <img src={ABoutImg} />
                                </div>
                            </div>
                        </Fade>
                    </Col>
                    <Col md="6" className="text1">
                        <Fade right>
                            <div className="about-text">
                                <h2>I'm Safarali and I'm a <span>Web Developer</span></h2>                    
                                {/* {portfolios.map(portfolio => (
                                    <p className="mt-3">{portfolio.abouttext}vcljhlkfgklh</p>
                                ))} */}
                                <p className="mt-3">My name is Safarali. I was born in Uzbekistan, 2001. I'm a Frontend developer. So I'm studying in marketing speciality of Tashkent State University of Economics since 2019. I started learning Web development in 2020. I learned JavaScript and its framework React.js. I can build responsive web-sites in React.js. My aim is to become a senior development and to carry out the great works in programming field. I'm keen on mobile development and I'm gonna learn it in the future, as well.</p>
                                <div>
                                    <a href={Resume} download="Safarali's CV" className="ui animated button mt-4">
                                        <div className="visible content">Download CV</div>
                                        <div className="hidden content">
                                            <i aria-hidden="true" className="download icon"></i> Resume
                                        </div>
                                    </a>
                                </div>
                            </div>
                        </Fade>
                    </Col>
                </Row>
            </div>
        </div>
    );
};

export default About;